import { Component, OnInit } from '@angular/core';

import { ValidateService } from '../../services/validate.service';
import { AuthService } from '../../services/auth.service';

import { FlashMessagesService } from 'angular2-flash-messages/module';

import { Router } from '@angular/router';

@Component({
  selector: 'app-register',
  templateUrl: './register.component.html',
  styleUrls: ['./register.component.css']
})
export class RegisterComponent implements OnInit {

  name: String;
  username: String;
  email: String;
  password: String;
  phone: Number;

  constructor(private validateService: ValidateService,
    private flashMessage: FlashMessagesService,
    private authService: AuthService,
    private router: Router
  ) { }

  ngOnInit() {
  }

  onRegisterSubmit() {
    // console.log(123);
    const user = {
      name: this.name,
      email: this.email,
      username: this.username,
      password: this.password,
      phone: this.phone
    };

    // Required fields

    if (!this.validateService.validateRegister(user)) {
      this.flashMessage.show('Please fill in all the fields', { cssClass: 'alert-danger', timeout: 3000 });
      return false;
    }

    // validate email

    if (!this.validateService.validateEmail(user.email)) {
      this.flashMessage.show('Please use valid email', { cssClass: 'alert-danger', timeout: 3000 });
      return;
    }

    // Register user
    this.authService.registerUser(user).subscribe(data => {
      if (data.success) {
        this.flashMessage.show('User successfully registered', { cssClass: 'alert-success', timeout: 3000 });
        this.router.navigate(['/login']);
      } else {
        this.flashMessage.show('Could not register', { cssClass: 'alert-danger', timeout: 3000 });
        this.router.navigate(['/register']);

      }
    });
  }
}



